<template>
  <div id="contato">
    <vs-row align="center" justify="center">
      <div class="container">
        <h1>Enviar e-mail</h1>
        <vs-row class="row-campos" justify="center" align="center">
          <vs-col type="flex" justify="center" align="center">
            <vs-input
              v-model="nome"
              class="inputx"
              placeholder="Nome completo"
            />
            <div class="divider-col" />
            <vs-input
              v-model="email"
              class="inputx"
              placeholder="Exemplo@Email.com"
            />
          </vs-col>
        </vs-row>

        <div class="divider-row" />

        <vs-row justify="center">
          <vs-col type="flex" justify="center" align="center">
            <v-textarea
              v-model="mensagem"
              class="textarea"
              solo
              rows="7"
              placeholder="Digite sua mensagem aqui..."
            />
          </vs-col>
        </vs-row>

        <div class="divider-row" />

        <vs-row justify="center">
          <vs-button
            :loading="loading"
            v-on:click="sendEmail()"
            size="xl"
            style="min-width: 375px; min-height: 60px"
          >
            Enviar
            <template #animate>
              <i class="fa fa-paper-plane"></i>
            </template>
          </vs-button>
        </vs-row>
      </div>
    </vs-row>
  </div>
</template>

<script>
import axios from "axios";
import Vue from "vue";

import Vuesax from "vuesax";
import "vuesax/dist/vuesax.css"; //Vuesax styles
import Vuetify from "vuetify";
import "vuetify/dist/vuetify.min.css";

Vue.use(Vuesax);
Vue.use(Vuetify);

export default {
  name: "Contato",
  data() {
    return {
      loading: false,
      nome: null,
      email: null,
      mensagem: null,
    };
  },
  methods: {
    notificacaoSend(position = null, color, nome) {
      this.$vs.notification({
        square: false,
        color,
        stick: true,
        duration: 10000,
        progress: "auto",
        position,
        buttonClose: true,
        title: "Mensagem",
        text:
          "Olá " +
          nome +
          ", Sua mensagem foi enviada para Victor Cassiano, Obrigado !",
        clickClose: true,
      });
    },
    notificacaoError(position = null) {
      this.$vs.notification({
        square: false,
        color: "danger",
        stick: true,
        duration: 10000,
        progress: "auto",
        position,
        buttonClose: true,
        title: "Mensagem",
        text: "Existem campos vazios",
        clickClose: true,
      });
    },
    sendEmail() {
      alert(this.mensagem);
      if (this.nome == null || this.email == null || this.mensagem == null) {
        this.notificacaoError();
      } else {
        this.loading = true;
        axios
          .post("https://formspree.io/f/mrgornwd", {
            Nome: this.nome,
            "E-mail": this.email.toLowerCase().trim(),
            _subject: `${this.nome} enviou uma mensagem pelo Portifolio.`,
            Mensagem: this.mensagem,
          })
          .then(() => {
            this.notificacaoSend(null, "success", this.nome);
            this.nome = "";
            this.email = "";
            this.mensagem = "";
            this.loading = false;
          })
          .catch((error) => {
            if (error) {
              // eslint-disable-next-line no-alert
              console.log(error); // => the response payload
            }
          });
      }
    },
  },
};
</script>

<style scoped>
h1 {
  color: white;
}
#contato {
  text-align: center;
  padding-left: 20%;
  padding-right: 20%;
  padding-bottom: 5%;
}
.row-campos {
  padding-top: 5%;
}
.container {
  border-radius: 75px;
  padding-bottom: 5%;
  padding-top: 5%;
  padding-left: min(15vw, 200px);
  padding-right: min(15vw, 200px);
  background-color: #7d33ff;
  min-width: 500px;
}
.textarea {
  font-size: 16px;
  font-family: Avenir, Helvetica, Arial, sans-serif;
  border-radius: 20px;
  padding: 2%;
  min-width: 420px;
  max-width: 420px;
  max-height: 460px;
}
.divider-row {
  padding-top: 2%;
}
.divider-col {
  padding-left: 2%;
}
</style>